.name {
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    color: white;

}

.header-wrapper {
    width: 100%;
    background: #16151C;
    z-index: 1;
    position: fixed;
}

.home-wrapper {
    position: relative;
}

.container-button {
    display: inline-block;
    padding-left: 10px;
}

h2 {
    padding-bottom: 10px;
}

.btn-home {
    color: white;
    /*margin: auto;*/
    /*margin-bottom: 10px;*/
    border: 2px solid;
    transition: all 0.4s ease-in-out;
    cursor: pointer;
}

.profile-links {
    margin-top: 20px;
    font-size: 50px;
}

.profile-links a {
    /*color: #A6E22E;*/
    color: gray;
    margin-right: 16px;
}

.profile-links a:hover {
    transition: all 0.5s;
    color: #A6E22E;
}

#right-arrow {
    display: inline-block;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    color: white;
    font-size: 15px;
}

.btn-home #right-arrow {
    transition: all 0.3s ease;
    right: 15px;
}

.btn-home:hover {
    background-color: #282c34;
    color: white;
}

.btn-home:hover #right-arrow {
    transform: rotateZ(90deg);
    transform-origin: center;
}

